// datetimemask.pattern: "The pattern can contain separator characters and the following placeholders: `m` - for month number; `mm` - for month number, with leading zero for single-digit values; `d` - for day of the month; `dd` - for day of the month, with leading zero for single-digit values; `yy` - for the last two digits of the year; `yyyy` - for a four-digit year." => "Kuvio voi sislt erotinmerkkej ja seuraavia paikkamerkkej: 'm' - kuukauden numero; 'mm' - kuukauden numero, jonka alussa on nolla yksinumeroisille arvoille; \"d\" - kuukauden piv; 'dd' - kuukauden piv, jonka alussa on nolla yksinumeroisille arvoille; \"yy\" - vuoden kaksi viimeist numeroa; \"VVVV\" - nelinumeroinen vuosi."
